import * as React from 'react';
import classNames from 'classnames';
import { RouteComponentProps } from '@reach/router';
import FormInput from '../../../common/components/form/FormInput';
import { changeUserPassword } from '../../user/forces/actions';
import { getCurrentUser } from '../forces/selectors';
import { connect } from 'react-redux';
import { RootState } from '../../../root.reducer';
import CallToActionButton from '../../../common/components/callToAction/CallToActionButton';
import FormAlert from '../../../common/components/form/FormAlert';
import { Form, FormikBag, withFormik, Field, FieldProps, FormikProps } from 'formik';
import { bindActionCreators } from 'redux';
import styles from './ChangePassword.module.css';

interface FormValues {
  currentPassword: string;
  newPassword: string;
}
type StateProps = ReturnType<typeof mapStateToProps>;
type Props = StateProps & FormValues & React.HTMLAttributes<HTMLElement> & RouteComponentProps;
type PropsWithFormik = Props & React.HTMLAttributes<HTMLElement> & FormikProps<FormValues>;

const ChangePassword: React.FunctionComponent<PropsWithFormik> = ({
  className,
  children,
  currentUser,
  ...props
}) => {
  if (currentUser == null) {
    return null;
  }

  return (
    <div className={classNames(styles.container, className)}>
      <Form>
        <Field
          name="currentPassword"
          render={({ field }: FieldProps<FormValues>) => (
            <FormInput
              {...field}
              styleType="secondary"
              type="password"
              name="currentPassword"
              required={true}
              placeholder="Nvrende passord"
            />
          )}
        />
        <Field
          name="newPassword"
          render={({ field }: FieldProps<FormValues>) => (
            <FormInput
              {...field}
              styleType="secondary"
              type="password"
              name="newPassword"
              required={true}
              placeholder="Nytt passord"
            />
          )}
        />
        <CallToActionButton
          className={styles.loginButton}
          styleType="secondary"
          disabled={
            props.isSubmitting ||
            props.values.currentPassword.length == 0 ||
            props.values.newPassword.length === 0
          }
          type="submit">
          Save
        </CallToActionButton>
        {<FormAlert error={props.error} success={props.status} />}
      </Form>
    </div>
  );
};

const formOptions = {
  mapPropsToValues: (props: Props) => ({
    currentPassword: '',
    newPassword: '',
  }),
  handleSubmit: async (
    values: { currentPassword: string; newPassword: string; error: string },
    formikBag: FormikBag<Props, FormValues>
  ) => {
    if (values.currentPassword === values.newPassword) {
      formikBag.resetForm();
      formikBag.setError('Nvrende passord og nytt passord kan ikke vre samme');
      formikBag.setSubmitting(false);
      return;
    }

    try {
      await formikBag.props.changeUserPassword(values);
      formikBag.setSubmitting(false);
      formikBag.resetForm();
      formikBag.setStatus('Passordet ditt har blitt oppdatert.');
    } catch (err) {
      formikBag.resetForm();
      formikBag.setError(err.message || 'Noe gikk galt');
      formikBag.setSubmitting(false);
    }
  },
};
const FormikLogin = withFormik<Props, FormValues>(formOptions)(ChangePassword);

const mapDispatchToProps = (dispatch: React.Dispatch) =>
  bindActionCreators(
    {
      changeUserPassword,
    },
    dispatch
  );

const mapStateToProps = (state: RootState) => ({
  currentUser: getCurrentUser(state),
});

export default connect<StateProps, {}, FormValues, RootState>(
  mapStateToProps,
  mapDispatchToProps
)(FormikLogin);
