import * as React from 'react';
import classNames from 'classnames';
import { RouteComponentProps } from '@reach/router';
import { H2, BodyText, H1 } from '../../../common/components/texts';
import FormInput from '../../../common/components/form/FormInput';
import config from '../../../config';
import { getCurrentHostname } from '../../../utils/url.utils';
import Routes from '../../../common/routing/Routes';
import { getCurrentUser } from '../forces/selectors';
import { connect } from 'react-redux';
import { RootState } from '../../../root.reducer';
import styles from './ReferFriend.module.css';

const getReferralRegisterURL = (referralCode: string) => {
  return `${getCurrentHostname()}${Routes.Register()}/?referralCode=${referralCode}`;
};

interface OwnProps {}
type StateProps = ReturnType<typeof mapStateToProps>;
type Props = StateProps & OwnProps & React.HTMLAttributes<HTMLElement> & RouteComponentProps;

const ReferFriend: React.FunctionComponent<Props> = ({
  className,
  children,
  currentUser,
  ...props
}) => {
  if (currentUser == null) {
    return null;
  }
  return (
    <div className={classNames(styles.container, className)}>
      <H2>F {config.referralCreditAward} olio-poeng for hver venn du verver!</H2>
      <BodyText>
        For hver venn du verver, fr begge {config.referralCreditAward} olio-poeng hver. Poengene
        dukker opp i profilen din nr vennen din har booket sin frste behandling.
        <br />
        Her er vervekoden din - spread the love!
      </BodyText>

      <H1>{currentUser.referralCode}</H1>

      <FormInput value={getReferralRegisterURL(currentUser.referralCode)} onChange={() => {}} />
    </div>
  );
};

const mapStateToProps = (state: RootState) => ({
  currentUser: getCurrentUser(state),
});

export default connect<StateProps, {}, OwnProps, RootState>(
  mapStateToProps,
  {}
)(ReferFriend);
