import * as React from 'react';
import { RouteComponentProps } from '@reach/router';
import { bindActionCreators, Dispatch } from 'redux';
import { connect } from 'react-redux';
import { BodyText, H1 } from '../../../common/components/texts';
import SectionSubtitle from '../../../common/components/section/SectionSubtitle';
import { RootState } from '../../../root.reducer';
import { getCurrentUser, userdataIsLoading, isLoggedIn } from '../forces/selectors';
import { fetchCurrentUserIfNeeded } from '../forces/actions';
import LoadingIndicator from '../../../common/components/loadingIndicator/LoadingIndicator';
import { getOlioPointsRounded } from '../../../utils/currency.utils';
import styles from './Mypoints.module.css';

interface OwnProps {}
type DispatchProps = ReturnType<typeof mapDispatchToProps>;
type StateProps = ReturnType<typeof mapStateToProps>;
type Props = DispatchProps &
  StateProps &
  OwnProps &
  React.HTMLAttributes<HTMLElement> &
  RouteComponentProps;

class Mypoints extends React.Component<Props> {
  componentDidMount() {
    if (this.props.isLoggedIn) {
      this.props.fetchCurrentUserIfNeeded();
    }
  }

  render() {
    const { currentUser, isLoading } = this.props;
    if (isLoading) {
      return <LoadingIndicator />;
    }
    if (currentUser == null) {
      return null;
    }
    return (
      <div className={styles.container}>
        <div className={styles.textcontainer}>
          <SectionSubtitle>
            Du har{' '}
            <span className={styles.pointsAmount}>{getOlioPointsRounded(currentUser.points)}</span>{' '}
            olio-poeng!
          </SectionSubtitle>
          <BodyText>
            Bruk de p din neste behandling eller spar de til en spesiell anledning. Huk av i boksen
            nr du bestiller hvis du vil ha rabattert pris.
          </BodyText>
        </div>
      </div>
    );
  }
}

const mapDispatchToProps = (dispatch: Dispatch) =>
  bindActionCreators(
    {
      fetchCurrentUserIfNeeded,
    },
    dispatch
  );

const mapStateToProps = (state: RootState) => ({
  currentUser: getCurrentUser(state),
  isLoggedIn: isLoggedIn(state),
  isLoading: userdataIsLoading(state),
});
export default connect<StateProps, DispatchProps, OwnProps, RootState>(
  mapStateToProps,
  mapDispatchToProps
)(Mypoints);
