import React from 'react';
import classNames from 'classnames';
import Link from './Link';
import { H4 } from '../texts';
import styles from './LinkButton.module.css';

export default ({
  header,
  to,
  disabled = false,
}: {
  header: string;
  to: string;
  disabled?: boolean;
}) => {
  const Component = disabled ? 'div' : Link;
  return (
    <Component to={to} className={classNames(styles.linkbutton, { [styles.disabled]: disabled })}>
      <H4 className={styles.text}>{header}</H4>
    </Component>
  );
};
