import * as React from 'react';
import { connect } from 'react-redux';
import { bindActionCreators, Dispatch } from 'redux';
import { RouteComponentProps } from '@reach/router';
import { PoseGroup } from 'react-pose';
import { updatePaymentInformation, getPaymentInformation } from '../forces/actions';
import PaymentContainer from './PaymentContainer';
import SuccessAnimation from '../../../common/animations/SuccessAnimation';
import ContainerAnimation from '../../../common/animations/ContainerAnimation';
import { RootState } from '../../../root.reducer';
import { hasPaymentInfo, getPaymentInfo } from '../../user/forces/selectors';
import { BodyText } from '../../../common/components/texts';
import USER_TEXTS from '../assets/texts';
import LoadingIndicator from '../../../common/components/loadingIndicator/LoadingIndicator';

interface OwnProps {}
type DispatchProps = ReturnType<typeof mapDispatchToProps>;
type StateProps = ReturnType<typeof mapStateToProps>;
type Props = DispatchProps &
  StateProps &
  OwnProps &
  React.HTMLAttributes<HTMLElement> &
  RouteComponentProps;

interface State {
  isPaymentSubmitted: boolean;
  isLoading: boolean;
}
class PaymentInformation extends React.Component<Props, State> {
  state = {
    isPaymentSubmitted: false,
    isLoading: false,
  };

  componentDidMount() {
    this.checkAndGetExistingPaymentInformation();
  }

  async checkAndGetExistingPaymentInformation() {
    const { hasSavedPayment, paymentInfo } = this.props;
    if (hasSavedPayment && paymentInfo && !paymentInfo.last4Digits) {
      this.setState({ isLoading: true });
      await this.props.getPaymentInformation();
      this.setState({ isLoading: false });
    }
  }

  getCardDigits() {
    const { paymentInfo } = this.props;
    return paymentInfo ? paymentInfo.last4Digits : undefined;
  }

  render() {
    return (
      <>
        {this.state.isPaymentSubmitted ? (
          <ContainerAnimation>
            <SuccessAnimation />
            <BodyText style={{ textAlign: 'center' }}>
              {USER_TEXTS.DASHBOARD.PAYMENT_INFORMATION.UPDATED}
            </BodyText>
          </ContainerAnimation>
        ) : (
          <ContainerAnimation>
            {this.props.hasSavedPayment ? (
              <BodyText>{USER_TEXTS.DASHBOARD.PAYMENT_INFORMATION.EXISTING}</BodyText>
            ) : (
              <BodyText>{USER_TEXTS.DASHBOARD.PAYMENT_INFORMATION.DOES_NOT_EXIST}</BodyText>
            )}
            {this.state.isLoading ? (
              <LoadingIndicator />
            ) : (
              <PaymentContainer
                buttonText={USER_TEXTS.DASHBOARD.PAYMENT_INFORMATION.BUTTONS.UPDATE}
                existingLastFourCardDigits={this.getCardDigits()}
                hideSkipButton
                onSubmitCardToken={async cardToken => {
                  await this.props.updatePaymentInformation({ cardToken });
                }}
                onPaymentSubmitted={() => {
                  this.setState({ isPaymentSubmitted: true });
                }}
              />
            )}
          </ContainerAnimation>
        )}
      </>
    );
  }
}

const mapStateToProps = (state: RootState) => ({
  hasSavedPayment: hasPaymentInfo(state),
  paymentInfo: getPaymentInfo(state),
});
const mapDispatchToProps = (dispatch: Dispatch) =>
  bindActionCreators(
    {
      updatePaymentInformation,
      getPaymentInformation,
    },
    dispatch
  );

export default connect<StateProps, DispatchProps, OwnProps, RootState>(
  mapStateToProps,
  mapDispatchToProps
)(PaymentInformation);
