import * as React from 'react';
import classNames from 'classnames';
import { RouteComponentProps } from '@reach/router';
import FloatingLabel from '../../../common/components/floatingLabel/FloatingLabel';
import { updateProfileInformation } from '../../user/forces/actions';
import { getCurrentUser } from '../forces/selectors';
import { connect } from 'react-redux';
import { RootState } from '../../../root.reducer';
import { Form, FormikBag, withFormik, Field, FieldProps, FormikProps, Formik } from 'formik';
import { bindActionCreators } from 'redux';
import CallToActionButton from '../../../common/components/callToAction/CallToActionButton';
import FormAlert from '../../../common/components/form/FormAlert';
import { Dispatch } from 'redux';
import styles from './Profile.module.css';

interface FormValues {
  name: string;
  email: string;
  phoneNumber: string;
}

interface OwnProps {}
type StateProps = ReturnType<typeof mapStateToProps>;
type Props = StateProps & OwnProps & React.HTMLAttributes<HTMLElement> & RouteComponentProps;
type PropsWithFormik = Props & React.HTMLAttributes<HTMLElement> & FormikProps<FormValues>;

const Profile: React.FunctionComponent<PropsWithFormik> = ({
  className,
  children,
  currentUser,
  ...props
}) => {
  const { name, email, phoneNumber } = { ...currentUser };
  return (
    <div className={styles.container}>
      <Formik
        enableReinitialize
        initialValues={{ name: name, email: email, phoneNumber: phoneNumber }}
        onSubmit={async (values, actions) => {
          try {
            await props.updateProfileInformation(values);
            actions.setSubmitting(false);
            actions.setStatus({ message: 'Profilen din har blitt oppdatert.' });
          } catch (err) {
            actions.setErrors({ message: 'Oops! Noe gikk galt.' });
            actions.setSubmitting(false);
          }
        }}
        render={({ values, handleChange, status, errors }) => (
          <Form>
            <div className={styles.form}>
              <Field>
                {() => (
                  <FloatingLabel
                    value={values.name}
                    onChange={handleChange}
                    label="Navn"
                    type="text"
                    styleType="primary"
                    labelStyleType="primary"
                    name="name"
                  />
                )}
              </Field>
              <Field>
                {() => (
                  <FloatingLabel
                    value={values.email}
                    onChange={handleChange}
                    label="Epost"
                    type="text"
                    styleType="primary"
                    labelStyleType="primary"
                    name="email"
                  />
                )}
              </Field>
              <Field>
                {() => (
                  <FloatingLabel
                    value={values.phoneNumber}
                    onChange={handleChange}
                    label="Telefonnummer"
                    type="text"
                    styleType="primary"
                    labelStyleType="primary"
                    name="phoneNumber"
                  />
                )}
              </Field>
              <CallToActionButton
                className={styles.loginButton}
                styleType="secondary"
                type="submit">
                Lagre
              </CallToActionButton>
            </div>
            {status && status.message ? <FormAlert success={status.message} /> : null}
            {errors && errors.message ? <FormAlert error={errors.message} /> : null}
          </Form>
        )}
      />
    </div>
  );
};

const mapDispatchToProps = (dispatch: Dispatch) =>
  bindActionCreators(
    {
      updateProfileInformation,
    },
    dispatch
  );

const mapStateToProps = (state: RootState) => ({
  currentUser: getCurrentUser(state),
  updateProfileInformation: updateProfileInformation,
});

export default connect<StateProps, {}, FormValues, RootState>(
  mapStateToProps,
  mapDispatchToProps
)(Profile);
