import * as React from 'react';
import { connect } from 'react-redux';
import { bindActionCreators, Dispatch } from 'redux';
import { RouteComponentProps } from '@reach/router';
import { RootState } from '../../../root.reducer';
import { getCurrentUser, isUserGuest } from '../forces/selectors';
import { logout } from '../forces/actions';
import { isPathNameWhitelistedForGuests } from '../../../utils/routes.utils';

interface RouterProps {
  userId: string;
}
interface OwnProps {}
type DispatchProps = ReturnType<typeof mapDispatchToProps>;
type StateProps = ReturnType<typeof mapStateToProps>;
type Props = DispatchProps &
  StateProps &
  OwnProps &
  React.HTMLAttributes<HTMLElement> &
  RouteComponentProps<RouterProps>;

class GuestAuthListener extends React.Component<Props> {
  componentWillUnmount() {
    // Log the user out when navigating away from the user route
    const pathException = this.checkWhiteListedRoutes();
    const isGuest = this.props.isGuest;
    if (pathException && isGuest) {
      this.props.logout();
    }
  }

  checkWhiteListedRoutes(): boolean {
    return this.props.location
      ? isPathNameWhitelistedForGuests(this.props.location.pathname)
      : false;
  }

  render() {
    const { children } = this.props;
    return children;
  }
}

const mapStateToProps = (state: RootState) => ({
  user: getCurrentUser(state),
  isGuest: isUserGuest(state),
});
const mapDispatchToProps = (dispatch: Dispatch) =>
  bindActionCreators(
    {
      logout,
    },
    dispatch
  );

export default connect<StateProps, DispatchProps, OwnProps, RootState>(
  mapStateToProps,
  mapDispatchToProps
)(GuestAuthListener);
