import * as React from 'react';
import { connect } from 'react-redux';
import { Router as ReachRouter } from '@reach/router';
import UserPageLayout from '../../../common/components/layout/UserPageLayout';
import Routes, { RoutePaths } from '../../../common/routing/Routes';
import Bookings from '../../bookings/containers/Bookings';
import Dashboard from '../containers/Dashboard';
import Mypoints from '../containers/Mypoints';
import Bookinginfo from '../containers/Bookinginfo';
import ReferFriend from '../containers/ReferFriend';
import MembershipStatus from '../containers/MembershipStatus';
import Profile from '../containers/Profile';
import PaymentInformation from '../containers/PaymentInformation';
import ChangePassword from '../containers/ChangePassword';
import NotFoundRoute from '../../../common/routing/NotFoundRoute';
import PrivateRoute from '../containers/PrivateRoute';
import { UserRole } from '../forces/types';
import GuestAuthListener from '../containers/GuestAuthListener';
import { getCurrentUser } from '../forces/selectors';
import { RootState } from '../../../root.reducer';
import Booking from '../../booking/containers/Booking';
import USER_TEXTS from '../assets/texts';

const memberTabs = [
  { route: Routes.Dashboard(), title: 'oversikt' },
  { route: Routes.Bookings(), title: 'bookings' },
  { route: Routes.MyPoints(), title: 'olio-poeng' },
];

interface OwnProps {}
type StateProps = ReturnType<typeof mapStateToProps>;
type Props = StateProps & OwnProps & React.HTMLAttributes<HTMLElement>;

const UserRouter = (props: Props) => {
  const showLogoutButton = !!(props.user && props.user.role === UserRole.Member);
  const tabs = props.user && props.user.role === UserRole.Member ? memberTabs : [];
  return (
    <UserPageLayout
      title="Min olio"
      tabs={tabs}
      showBookButton
      showLogoutButton={showLogoutButton}
      user={props.user}
      {...props}>
      <ReachRouter>
        <GuestAuthListener path="/">
          <NotFoundRoute default />
          <PrivateRoute path={RoutePaths.Dashboard} component={Dashboard} />
          <PrivateRoute
            roles={[UserRole.Guest, UserRole.Member]}
            path={RoutePaths.Bookings}
            component={Bookings}
          />
          <PrivateRoute
            roles={[UserRole.Guest, UserRole.Member]}
            path={RoutePaths.BookingInfo}
            component={Bookinginfo}
          />
          <PrivateRoute
            roles={[UserRole.Guest, UserRole.Member]}
            path={RoutePaths.EditBooking}
            component={Booking}
            message={USER_TEXTS.MESSAGES.LOGIN.TO_UPDATE_BOOKING}
          />
          <PrivateRoute path={RoutePaths.MyPoints} component={Mypoints} />
          <PrivateRoute path={RoutePaths.ReferFriend} component={ReferFriend} />
          <PrivateRoute path={RoutePaths.UpdatePaymentInformation} component={PaymentInformation} />
          <PrivateRoute path={RoutePaths.Profile} component={Profile} />
          <PrivateRoute path={RoutePaths.Membership} component={MembershipStatus} />
          <PrivateRoute path={RoutePaths.ChangePassword} component={ChangePassword} />
        </GuestAuthListener>
      </ReachRouter>
    </UserPageLayout>
  );
};

const mapStateToProps = (state: RootState) => ({
  user: getCurrentUser(state),
});

export default connect<StateProps, {}, OwnProps, RootState>(
  mapStateToProps,
  {}
)(UserRouter);
