import * as React from 'react';
import { navigate } from 'gatsby';
import Routes from '../../../common/routing/Routes';
import BookingOverview from '../../booking/components/BookingOverview';
import Link from '../../../common/components/link/Link';
import { connect } from 'react-redux';
import { RootState } from '../../../root.reducer';
import { fetchBookingIfNeeded, cancelBooking } from '../../bookings/forces/actions';
import { getBooking, isBookingsLoading } from '../../bookings/forces/selectors';
import { changeSelectedClinicId } from '../../booking/forces/actions';
import { fetchAllClinics } from '../../clinics/forces/actions';
import { bindActionCreators, Dispatch } from 'redux';
import {
  getTotalPrice,
  getTotalDuration,
  getTreatmentsForServiceIds,
} from '../../treatment/forces/selectors';
import { getAllTreatmentTypes } from '../../treatment/forces/actions';
import { Service, Booking } from '../forces/types';
import { RouteComponentProps } from '@reach/router';
import LoadingIndicator from '../../../common/components/loadingIndicator/LoadingIndicator';
import ConfirmActionModal from '../../../common/components/modal/ConfirmActionModal';
import { showSnackbar } from '../../ui/forces/actions';
import { isUserGuest } from '../forces/selectors';

import USER_TEXTS from '../assets/texts';
import { BookingContext } from '../../booking/forces/types';
import styles from './Bookinginfo.module.css';
import { getSelectedClinic } from '../../clinics/forces/selectors';

type OwnProps = { id: string; waitlistId: string };
type DispatchProps = ReturnType<typeof mapDispatchToProps>;
type StateProps = ReturnType<typeof mapStateToProps>;
type Props = OwnProps &
  React.HTMLAttributes<HTMLElement> &
  StateProps &
  DispatchProps &
  RouteComponentProps<OwnProps>;

type State = {
  isCancelBookingModalOpen: boolean;
};

class BookingInfo extends React.Component<Props, State> {
  state = {
    isCancelBookingModalOpen: false,
  };

  componentDidMount() {
    const { id, waitlistId } = this.props;
    if (id == null || waitlistId == null) {
      throw new Error('id and waitlistId must be set');
    }
    this.props.getAllTreatmentTypes();
    this.props.fetchAllClinics();
    this.props.fetchBookingIfNeeded({ id, waitlistId });
  }

  async cancelBooking(booking: Booking) {
    await this.props.cancelBooking(booking);
    this.props.showSnackbar('Vi hper  se deg snart igjen!');
    navigate(Routes.Bookings());
  }

  renderCancelBookingModal(booking: Booking) {
    return (
      <ConfirmActionModal
        isOpen={this.state.isCancelBookingModalOpen}
        handleResponse={response => {
          this.setState({ isCancelBookingModalOpen: false });
          if (response) {
            this.cancelBooking(booking);
          }
        }}
        showButtons={true}
      />
    );
  }

  changeBookingButton(
    clinicId: string,
    bookingId: string,
    isGuest: boolean | null
  ): { text: string; callback: () => void } {
    const editBookingUrl = `/booking/update/${clinicId}/${bookingId}`; // - old - open - `/user/booking/update/${bookingId}`;
    const redirectToEditBooking = () => navigate(editBookingUrl);
    const toggleCancelModal = () => {
      this.setState({ isCancelBookingModalOpen: true });
    };
    const button = {
      text: USER_TEXTS.BUTTONS.BOOKING.CHANGE,
      callback: redirectToEditBooking,
    };
    return button;
  }

  render() {
    const { className, booking, isLoading, isGuest, ...props } = this.props;
    if (isLoading) {
      return <LoadingIndicator />;
    }
    if (booking == null) {
      return null;
    }

    const totalPrice = this.props.getTotalPrice(booking.serviceIds);
    const totalDuration = this.props.getTotalDuration(booking.serviceIds);
    const mainButton = this.changeBookingButton(booking.waitlistId, booking.id, isGuest);
    const bookingServiceIds = booking.serviceIds.map(service => service.id);
    const selectedTreatments = getTreatmentsForServiceIds(bookingServiceIds)(this.props.state);
    const selectedClinic = getSelectedClinic(this.props.state, booking.waitlistId);

    return (
      <>
        <Link to={Routes.Bookings()} className={styles.exitbutton} />
        <BookingOverview
          selectedDate={booking.time}
          selectedTime={booking.timeOfDayinMilliseconds}
          totalPrice={totalPrice}
          totalDuration={totalDuration}
          selectedTreatments={selectedTreatments}
          selectedClinic={selectedClinic}
          leftButton={{
            callbackText: mainButton.text,
            callback: mainButton.callback,
            validation: true,
            styleType: 'secondary',
            size: 'large',
          }}
          bookingContext={BookingContext.DASHBOARD}
        />
        {isGuest && this.renderCancelBookingModal(booking)}
      </>
    );
  }
}

const mapStateToProps = (state: RootState, ownProps: OwnProps) => ({
  booking: getBooking(state, ownProps.id),
  getTotalPrice: (services: Array<Service>) => getTotalPrice(services)(state),
  getTotalDuration: (services: Array<Service>) => getTotalDuration(services)(state),
  isGuest: isUserGuest(state),
  isLoading: isBookingsLoading(state),
  state,
});

const mapDispatchToProps = (dispatch: Dispatch) =>
  bindActionCreators(
    {
      fetchBookingIfNeeded,
      cancelBooking,
      showSnackbar,
      getAllTreatmentTypes,
      fetchAllClinics,
      changeSelectedClinicId,
    },
    dispatch
  );

export default connect<StateProps, DispatchProps, OwnProps, RootState>(
  mapStateToProps,
  mapDispatchToProps
)(BookingInfo);
