import * as React from 'react';
import { connect } from 'react-redux';
import { bindActionCreators, Dispatch } from 'redux';
import { RouteComponentProps } from '@reach/router';
import Routes from '../../../common/routing/Routes';
import LinkButton from '../../../common/components/link/LinkButton';
import { RootState } from '../../../root.reducer';
import { fetchBookings } from '../forces/actions';
import { getBookings, isBookingsLoading } from '../forces/selectors';
import {
  formatToDateAndTime,
  formatToDateAndTimeLong,
  isDateInThePast,
} from '../../../utils/date.utils';
import LoadingIndicator from '../../../common/components/loadingIndicator/LoadingIndicator';
import EmptyState from '../../../common/components/emptyState/EmptyState';

interface OwnProps {}
type DispatchProps = ReturnType<typeof mapDispatchToProps>;
type StateProps = ReturnType<typeof mapStateToProps>;
type Props = DispatchProps &
  StateProps &
  OwnProps &
  React.HTMLAttributes<HTMLElement> &
  RouteComponentProps;

class Bookings extends React.Component<Props> {
  componentDidMount() {
    this.props.fetchBookings();
  }

  private sortBookings(bookings: Array<Booking>) {
    return this.props.bookings.sort((a, b) => a.time - b.time);
  }

  render() {
    const { bookings, isLoading } = this.props;
    if (isLoading) {
      return <LoadingIndicator />;
    }
    if (bookings.length === 0) {
      return <EmptyState message="Du har ikke booket time enda." />;
    }
    return (
      <>
        {this.sortBookings(bookings).map(booking => (
          <LinkButton
            key={booking.id}
            header={`${formatToDateAndTimeLong(booking.time)}`}
            to={Routes.BookingInfo({ id: booking.id, waitlistId: booking.waitlistId })}
            disabled={isDateInThePast(booking.time)}
          />
        ))}
      </>
    );
  }
}

const mapStateToProps = (state: RootState) => ({
  bookings: getBookings(state),
  isLoading: isBookingsLoading(state),
});

const mapDispatchToProps = (dispatch: Dispatch) =>
  bindActionCreators(
    {
      fetchBookings,
    },
    dispatch
  );

export default connect<StateProps, DispatchProps, OwnProps, RootState>(
  mapStateToProps,
  mapDispatchToProps
)(Bookings);
