import React, { useState } from 'react';
import { RouteComponentProps } from '@reach/router';
import { updateMembershipStatus } from '../../user/forces/actions';
import { getCurrentUser } from '../forces/selectors';
import { connect } from 'react-redux';
import { RootState } from '../../../root.reducer';
import { bindActionCreators, Dispatch } from 'redux';
import Modal from '../../../common/components/modal/Modal';
import CallToActionButton from '../../../common/components/callToAction/CallToActionButton';
import styles from './MembershipStatus.module.css';

interface OwnProps {}
type StateProps = ReturnType<typeof mapStateToProps>;
type DispatchProps = ReturnType<typeof mapDispatchToProps>;
type Props = DispatchProps &
  StateProps &
  OwnProps &
  React.HTMLAttributes<HTMLElement> &
  RouteComponentProps;

const MembershipStatus: React.FunctionComponent<Props> = ({
  className,
  children,
  currentUser,
  ...props
}) => {
  const [isCancelled, setIsCancelled] = useState(currentUser.cancellationDate ? true : false);
  const [isModalOpen, setIsModalOpen] = useState(false);

  const setMembershipStatus = async () => {
    await props.updateMembershipStatus();
    setIsCancelled(!isCancelled);
    isModalOpen ? openOrCloseModal() : null;
  };

  const openOrCloseModal = () => {
    setIsModalOpen(!isModalOpen);
  };

  return (
    <div className={styles.container}>
      {isCancelled ? (
        <p onClick={setMembershipStatus} className={styles.membershipButton}>
          Fortsett medlemskap
        </p>
      ) : (
        <p onClick={openOrCloseModal} className={styles.membershipButton}>
          Kanseller medlemskap
        </p>
      )}
      <Modal isOpen={isModalOpen}>
        <div className={styles.modal}>
          <h2 className={styles.sikker}>Er du sikker?</h2>
          <p>
            Om du sier opp medlemskapet ditt hos olio, vil du automatisk miste oppsparte olio-poeng.
            Husk at et medlemskap hos oss er gratis!
          </p>
          <div className={styles.buttonContainer}>
            <CallToActionButton
              className={styles.membershipButton}
              styleType="primary"
              onClick={() => openOrCloseModal()}>
              Fortsett medlemskap
            </CallToActionButton>
            <CallToActionButton
              className={styles.membershipButton}
              styleType="primary"
              onClick={() => setMembershipStatus()}>
              Bekreft kansellering
            </CallToActionButton>
          </div>
        </div>
      </Modal>
    </div>
  );
};

const mapDispatchToProps = (dispatch: Dispatch) =>
  bindActionCreators(
    {
      updateMembershipStatus,
    },
    dispatch
  );

const mapStateToProps = (state: RootState) => ({
  currentUser: getCurrentUser(state),
  updateMembershipStatus: updateMembershipStatus,
});

export default connect<StateProps, DispatchProps, OwnProps, RootState>(
  mapStateToProps,
  mapDispatchToProps
)(MembershipStatus);
